import { useEffect, useState } from "react";
import ItemList from './ItemList';

const AllItems = () => {
  
    const[isAdded, setIsAdded]= useState(true);
    const [newData, setNewData]= useState([])

  useEffect(()=> {
      setIsAdded(true);
    fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/shoppinglist.json'
    ).then(response => {
        return response.json();
    }).then(data => {
         const items = [];

         for (const key in data){
             const item= {
                id:key,
                ...data[key]
             };
             items.push(item);
         }
         


         setIsAdded(false);
         setNewData(items);
    });
  }, []);

    if(isAdded) {
        return(
        <p>Adding...</p>
        );
    }



    return(
        <div className='container'>
          <ul >
              <ItemList items={newData}/>
          </ul>
        </div>
    )
}

export default AllItems;